<template>
  <el-dialog v-model="editDialogVisible" :title="title" :before-close="handleClose" width="600px" draggable>
    <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        inline
        label-width="80px"
        label-position="right"
        status-icon>
      <el-form-item label="所属科室" prop="departmentId" style="width: 48%;">
        <el-select v-model="editForm.departmentId" placeholder="请选择所属科室">
          <el-option label="顶级科室" :value="0"/>
          <el-option v-for="department in departmentList" :key="department.id" :label="department.name"
                     :value="department.id"/>
        </el-select>
      </el-form-item>
<!--      <el-form-item label="状态" prop="disabled" style="width: 40%;">-->
<!--        <el-switch-->
<!--            v-model="editForm.disabled"-->
<!--            style="&#45;&#45;el-switch-on-color: #13ce66; &#45;&#45;el-switch-off-color: #ff4949"-->
<!--            active-text="启用"-->
<!--            inactive-text="禁用"-->
<!--            :active-value="false"-->
<!--            :inactive-value="true"-->
<!--            width="50px"-->
<!--            disabled-->
<!--        />-->
<!--      </el-form-item>-->
      <el-form-item label="诊室号" prop="roomNo" style="width: 100%;">
        <el-input v-model="editForm.roomNo" type="number"/>
      </el-form-item>
      <el-form-item label="备注" prop="note" style="width: 100%;">
        <el-input v-model="editForm.note" type="textarea" :rows="5"/>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="handleReset(id)">重置</el-button>
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleConfirm">确定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script setup>
import {defineProps, defineEmits, computed, ref, watch, reactive} from 'vue';
import AxiosUtil from "@/utils/AxiosUtil";
import {ElMessageBox, ElMessage} from "element-plus";

// 定义传入该子组件的参数
const props = defineProps({
  editDialogVisible: {
    type: Boolean,
    required: true,
    default: false
  },
  id: {
    type: Number,
    required: true,
    default: -1
  },
  title: {
    type: String,
    required: true,
    default: ''
  }
});

// 定义组件（自定义）事件
const emit = defineEmits(['update:editDialogVisible', 'handleSubmit']);

// 定义“编辑”对话框的显示状态
const editDialogVisible = computed({
  // 只读！读取时从父组件获取，更新时更新父组件
  get() {
    return props.editDialogVisible;
  },
  set(newValue) {
    emit('update:editDialogVisible', newValue);
  }
});

// 定义表单数据
const editForm = ref({
  id: -1,
  roomNo: '',
  departmentId: 0,
  disabled: false,
  note: '',
});

// 定义科室列表
const departmentList = ref([]);

const validateRoom = async (rule, value, callback) => {
  // console.log(rule.field)
  // console.log(value)

  let url;
  // 基础格式验证通过后，才给 url 赋值。
  if (rule.field == 'roomNo') {
    if (value && value.length > 8) {
      callback(new Error('诊室号长度不可超过8位'));
    } else {
      url = '/api/bsns/rooms/checkRoom?id=' + editForm.value.id + '&roomNo=' + value;
    }
  }
  // url 有值，才向后台发送验证请求。
  // console.log(url);
  if (url) {
    // console.log('向后台发送请求');
    let res = await AxiosUtil.post(url);
    // console.log(res.data);
    if (res.data.code == 1) {
      callback(new Error(res.data.msg));
    } else {
      callback();
    }
  }
}

// 定义表单验证规则
const editFormRules = reactive({
  departmentId: [
    {required: true, message: '所属科室不能为空', trigger: 'blur'},
  ],
  roomNo: [
    {required: true, message: '诊室号不能为空', trigger: 'blur'},
    {validator: validateRoom, trigger: 'change'},
    {validator: validateRoom, trigger: 'blur'},
  ],
  disabled: [
    {required: true, message: '状态不能为空', trigger: 'blur'},
  ],
  note: [
    {required: false, max: 192, message: '备注信息不可超过192个字符', trigger: 'blur'},
  ],
})

// 定义表单引用
const editFormRef = ref(null);

// 表单重置事件
const handleReset = async (id) => {
  if (id == -1) {
    editForm.value = {
      id: -1,
      roomNo: '',
      departmentId: 0,
      disabled: false,
      note: '',
    };
    console.log('清空表单...');
  } else {
    let res = await AxiosUtil.get('/api/bsns/rooms/' + id);
    console.log(res.data);
    if (res.data.code == 20010) {
      let room = res.data.room;
      // 更新表格数据
      editForm.value = room;
    } else {
      ElMessage({
        message: res.data.msg,
        type: 'error',
      });
    }
    console.log('从后台填充...');
  }
  // 无论是新增还是编辑，都需要向后台请求科室列表
  let res = await AxiosUtil.get('/api/bsns/departments');
  console.log(res.data);
  if (res.data.code == 20010) {
    let departments = [];
    res.data.departmentList.forEach(item => {
      item.name = "+ " + item.name;
      departments.push(item);
      item.children.forEach(child => {
        child.name = "••• " + child.name;
        departments.push(child);
      })
    });
    // 更新科室列表
    departmentList.value = departments;
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
}

// 定义关闭对话框前的确认操作
const handleClose = (done) => {
  ElMessageBox.confirm('确认在未保存的情况下关闭对话框？')
      .then(() => {
        done()
      })
      .catch(() => {
        // catch error
      })
}

// 定义对话框关闭事件
const handleCancel = () => {
  editDialogVisible.value = false;
}

// 定义确认按钮事件
const handleConfirm = () => {
  // 表单验证
  editFormRef.value.validate(async valid => {
    if (valid) {
      // 表单验证通过
      let url, res;
      if (editForm.value.id == -1) {
        console.log('提交新增...');
        url = '/api/bsns/rooms';
        res = await AxiosUtil.post(url, editForm.value);
        console.log("新增诊室 res.data= ", res.data);
      } else {
        console.log('提交修改...');
        url = '/api/bsns/rooms';
        res = await AxiosUtil.put(url, editForm.value);
        console.log("修改诊室 res.data= ", res.data);
      }
      if (res.data.code == 20020 || res.data.code == 20030) {
        ElMessage({
          message: res.data.msg,
          type: 'success',
        });
        emit('handleSubmit'); // 触发自定义事件，使父组件刷新数据
        handleCancel(); // 关闭对话框
      } else {
        ElMessage({
          message: res.data.msg,
          type: 'error',
        });
      }
    }
  });
}

// 当对话框重新显示时重置表单
watch(editDialogVisible, (newValue) => {
  if (newValue == true) {
    handleReset(props.id);
  }
})

</script>


<script>
export default {
  name: "EditDialog"
}
</script>

<style scoped>

</style>