<template>
  <el-container>
    <!--操作台-->
    <el-header class="operatingFloor">
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input v-model="query" placeholder="搜索诊室..." @keyup.enter="handleQuery" clearable/>
        </el-col>
        <el-button type="primary" :icon="Search" @click="handleQuery">查询</el-button>
        <el-button type="success" :icon="DocumentAdd" @click="handleEdit(null)"
                   v-if="hasAnyAuthority('business:room:add')">新增
        </el-button>
        <el-button type="warning" :icon="Paperclip" @click="handleChangeDepartment" :disabled="batchDeleteBtnDisabled"
                   v-if="hasAnyAuthority('business:room:edit')">批量更改科室
        </el-button>
        <el-popconfirm title="您确定要批量删除这些记录吗？" @confirm="handleDelete(null)"
                       v-if="hasAnyAuthority('business:room:delete')">
          <template #reference>
            <el-button type="danger" :icon="Delete" :disabled="batchDeleteBtnDisabled">批量删除</el-button>
          </template>
        </el-popconfirm>
      </el-row>
    </el-header>
    <el-main style="padding: 5px; background: aliceblue;" id="mainView">
      <el-table
          :data="tableData"
          :max-height="tableMaxHeight"
          :default-sort="{ prop: 'id', order: 'ascending' }"
          size="default"
          @selection-change="handleSelectionChange"
          style="width: 100%"
          highlight-current-row
          default-expand-all
          stripe>
        <el-table-column type="selection" width="40" align="center"/>
        <el-table-column prop="roomNo" label="诊室号" width="100" header-align="center" sortable fixed/>
        <el-table-column prop="departmentInfo.name" label="所属科室" width="120" header-align="center" sortable fixed/>
        <el-table-column prop="note" label="备注" width="200" header-align="center"/>
<!--        <el-table-column prop="disabled" label="状态" width="76" align="center"-->
<!--                         :filters="[{ text: '启用', value: false },{ text: '禁用', value: true },]"-->
<!--                         :filter-method="filterState">-->
<!--          <template #default="{row}">-->
<!--            <el-switch-->
<!--                v-model="row.disabled"-->
<!--                inline-prompt-->
<!--                style="&#45;&#45;el-switch-on-color: #13ce66; &#45;&#45;el-switch-off-color: #ff4949"-->
<!--                active-text="启用"-->
<!--                inactive-text="禁用"-->
<!--                :active-value="false"-->
<!--                :inactive-value="true"-->
<!--                width="50px"-->
<!--                :loading="row.loading"-->
<!--                @change="handleStateChange(row)"-->
<!--                :disabled="!hasAnyAuthority('business:room:edit')"-->
<!--            />-->
<!--          </template>-->
<!--        </el-table-column>-->
        <el-table-column prop="createTime" label="创建时间" width="160" header-align="center" sortable/>
        <el-table-column prop="updateTime" label="更新时间" min-width="160" header-align="left" sortable/>
        <el-table-column label="操作" width="130" align="right" header-align="center" fixed="right">
          <template #default="scope">
            <el-button type="primary" :icon="Edit" @click="handleEdit(scope.row.id)"
                       v-if="hasAnyAuthority('business:room:edit')"/>
            <el-popconfirm title="您确定要删除这条记录吗？" @confirm="handleDelete(scope.row.id)"
                           v-if="hasAnyAuthority('business:room:delete')">
              <template #reference>
                <el-button type="danger" :icon="Delete"/>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    <el-footer style="background: powderblue;height: 32px;display: flex;align-items: center;justify-content: flex-end;">
      <el-pagination
          v-model:currentPage="queryForm.currentPage"
          v-model:page-size="queryForm.pageSize"
          :page-sizes="[15, 20, 50, 100]"
          small
          background
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </el-footer>
  </el-container>
  <EditDialog v-model:edit-dialog-visible="editDialogVisible" :id="editId" :title="editTitle"
              @handleSubmit="fetchData"/>
  <ChangeDepartmentDialog v-model:change-department-dialog-visible="changeDepartmentDialogVisible"
                          :room-list="multipleSelection" @handleSubmit="fetchData"/>
</template>

<script setup>
import {Search, DocumentAdd, Paperclip, Delete, Edit} from '@element-plus/icons-vue'
import {onMounted, reactive, ref, watch} from "vue";
import AxiosUtil from "@/utils/AxiosUtil";
import EditDialog from "./EditDialog";
import ChangeDepartmentDialog from "./ChangeDepartmentDialog";
import qs from "qs";
import {ElMessage} from "element-plus";

/*====以下为一些数据的定义====*/

// 定义表单的查询查询条件
const queryForm = reactive({
  query: '', // 查找的字符
  currentPage: 1, // 当前页数
  pageSize: 15, // 页面记录容量
});

// 定义临时的查询关键词
const query = ref('');

// 定义从分页组件的总记录数
const total = ref(40);

// 定义批量删除按钮是否禁用
const batchDeleteBtnDisabled = ref(true);

// 定义已选择的记录
const multipleSelection = ref([]);

// 定义表格的最大高度
const tableMaxHeight = ref(400);

// 定义表格数据
const tableData = ref([
  {
    id: 10075286712,
    roomNo: 103,
    departmentId: 0,
    departmentInfo: {
      id: 0,
      name: '顶级科室',
    },
    disabled: false,
    note: '通用诊室',
    createTime: '2022-09-23 20:10:01',
    updateTime: '2022-10-18 18:16:18',
  },
  {
    id: 1007448077,
    roomNo: 104,
    departmentId: 0,
    departmentInfo: {
      id: 0,
      name: '顶级科室',
    },
    disabled: false,
    note: '通用诊室2号',
    createTime: '2022-09-23 20:10:01',
    updateTime: '2022-10-18 18:16:18',
  },
]);

// 定义编辑对话框的显示状态
const editDialogVisible = ref(false);

// 定义批量修改科室对话框的显示状态
const changeDepartmentDialogVisible = ref(false);

// 定义传入编辑对话框的id值
const editId = ref(-1);

// 定义传入编辑对话框的title值
const editTitle = ref('');


/*====以下为各按钮的点击事件====*/

// 处理通过关键字查询
const handleQuery = () => {
  // 设置当前页码为1
  queryForm.currentPage = 1;
  // 由于设置了当queryForm发生变化时才从后台重新拉取数据，
  // 因此若查找的字符实际上未发生变化则需要手动刷新数据
  // 即先点查询按钮，再进行翻页等操作
  if (queryForm.query == query.value) {
    console.log('手动刷新：关键字不变...');
    fetchData();
  } else {
    queryForm.query = query.value;
  }
}

// 处理删除记录
const handleDelete = async (id) => {
  if (!id) {
    // 不传入参数时，为批量删除
    id = [];
    multipleSelection.value.forEach(item => {
      id.push(item.id);
    });
  } else {
    // 不传入参数时，为单个删除
    id = [id];
  }
  console.log('删除...', id);
  let url = '/api/bsns/rooms/' + id.join(',');
  console.log('url=', url);
  let res = await AxiosUtil.del(url);
  console.log('res=', res);
  if (res.data.code == 20040) {
    ElMessage({
      message: res.data.msg,
      type: 'success',
    });
    await fetchData();
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
}

// 处理新增、编辑
const handleEdit = (id) => {
  if (id) {
    console.log('修改...', id);
    editId.value = id;
    editTitle.value = '诊室修改';
  } else {
    console.log('新增...', id);
    editId.value = -1;
    editTitle.value = '诊室添加';
  }
  editDialogVisible.value = true;
}

// 处理批量修改科室
const handleChangeDepartment = (id) => {
  console.log('批量更改科室...', multipleSelection);
  changeDepartmentDialogVisible.value = true;
}


/*====以下为一些监听以及其他函数====*/

// 处理页面容量的改变
const handleSizeChange = (val) => {
  console.log(`${val} items per page`);
}

// 处理页码的改变
const handleCurrentChange = (val) => {
  console.log(`current page: ${val}`);
}

// 处理选择集的改变
const handleSelectionChange = (list) => {
  multipleSelection.value = list;
  // 更新批量删除按钮的可用状态
  batchDeleteBtnDisabled.value = list.length == 0;
  console.log('新选择= ', list);
}

// 处理状态的改变
const handleStateChange = async (row) => {
  // console.log('状态变化...', row.id);

  // 先设置元素为加载状态
  row.loading = true;
  let url = '/api/bsns/rooms/updateDisabled/' + row.id + '/' + row.disabled;
  // console.log(url);

  // 为保证程序能正常走完，发送请求要用 try...catch 包围
  try {
    let res = await AxiosUtil.put(url);
    if (res.data.code == 20030) {
      ElMessage({
        message: res.data.msg,
        type: 'success',
      });
    } else {
      ElMessage({
        message: res.data.msg,
        type: 'error',
      });
      row.disabled = !row.disabled; // 恢复显示为修改前的状态
    }
  } catch (e) {
    console.log('e=', e);
    ElMessage({
      message: '操作失败！' + e,
      type: 'error',
    });
    row.disabled = !row.disabled; // 恢复显示为修改前的状态
  }

  // 请求结束后取消元素的加载状态
  row.loading = false;
}

// 处理按状态筛选记录
const filterState = (value, row) => {
  return row.disabled === value;
}

// 从后台获取数据
const fetchData = async () => {
  let url = '/api/bsns/rooms?' + qs.stringify(queryForm);
  console.log('url=', url);
  // 从后台获取数据
  let res = await AxiosUtil.get(url);
  console.log("更新诊室列表 res= ", res);
  if (res.data.code == 20010) {
    // 更新表格数据
    tableData.value = res.data.roomList;
    // 更新分页组件总记录数
    total.value = res.data.total;
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
}

fetchData();

// 当表单的查询查询条件发生变化时，重新拉取数据
watch(queryForm, async (newForm, oldForm) => {
  // 如果改变每页容量，则确保当前页的第一条记录在新的结果中仍然出现
  if (newForm.pageSize != oldForm.pageSize) {
    let currentItem = oldForm.pageSize * (oldForm.currentPage - 1) + 1;
    newForm.currentPage = Math.ceil(currentItem / newForm.pageSize);
  }
  console.log(Date.now() + ' 重新拉取数据...');
  console.log(queryForm);
  await fetchData();
});

// 新建一个ResizeObserver用于监视表格容器尺寸的变化
const resizeObserver = new ResizeObserver((entries) => {
  for (const entry of entries) {
    // 仅对指定id的元素生效
    if (entry.target.id == 'mainView') {
      let mainViewHeight;
      // 由于兼容性问题，获取元素高度时需谨慎
      // https://developer.mozilla.org/zh-CN/docs/Web/API/ResizeObserver
      // https://developer.mozilla.org/zh-CN/docs/Web/API/ResizeObserverEntry
      if (entry.contentBoxSize) {
        // Firefox implements `contentBoxSize` as a single content rect, rather than an array
        const contentBoxSize = Array.isArray(entry.contentBoxSize) ? entry.contentBoxSize[0] : entry.contentBoxSize;
        mainViewHeight = contentBoxSize.blockSize;
      } else {
        mainViewHeight = entry.contentRect.height;
      }
      // 设置表格的最大高度为表格容器的高度
      tableMaxHeight.value = mainViewHeight;
      // console.log('mainViewHeight= ', mainViewHeight);
    }
  }
});

// 在组件完成初始渲染并创建 DOM 节点后开始监视表格容器
onMounted(() => {
  const mainView = document.getElementById('mainView');
  resizeObserver.observe(mainView);
});


</script>

<script>
export default {
  name: "index"
}
</script>

<style scoped>
/*操作台*/
.operatingFloor {
  /*display: flex;*/
  /*align-items: center; !*align-items 属性可以使元素在交叉轴方向对齐*!*/
  /*justify-content: flex-start; !*justify-content属性用来使元素在主轴方向上对齐*!*/
  height: 48px;
  padding: 8px 10px;

}

.el-tag--small {
  margin-left: 5px;
}

</style>