<template>
  <el-dialog v-model="changeDepartmentDialogVisible" title="批量修改科室" :before-close="handleClose" width="600px" draggable>
    <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        inline
        label-width="80px"
        label-position="right"
        status-icon>
      <el-form-item label="所属科室" prop="departmentId" style="width: 48%;">
        <el-select v-model="editForm.departmentId" placeholder="请选择所属科室">
          <el-option label="顶级科室" :value="0"/>
          <el-option v-for="department in departmentList" :key="department.id" :label="department.name"
                     :value="department.id"/>
        </el-select>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="handleReset">重置</el-button>
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleConfirm">确定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script setup>
import {defineProps, defineEmits, computed, ref, watch, reactive} from 'vue';
import AxiosUtil from "@/utils/AxiosUtil";
import {ElMessageBox, ElMessage} from "element-plus";

// 定义传入该子组件的参数
const props = defineProps({
  changeDepartmentDialogVisible: {
    type: Boolean,
    required: true,
    default: false
  },
  // 诊室列表
  roomList: {
    type: Array,
    required: true,
    default: () => []
  },
});

// 定义组件（自定义）事件
const emit = defineEmits(['update:changeDepartmentDialogVisible', 'handleSubmit']);

// 定义“编辑”对话框的显示状态
const changeDepartmentDialogVisible = computed({
  // 只读！读取时从父组件获取，更新时更新父组件
  get() {
    return props.changeDepartmentDialogVisible;
  },
  set(newValue) {
    emit('update:changeDepartmentDialogVisible', newValue);
  }
});

// 定义表单的查询查询条件
const editForm = ref({
  id: [],
  departmentId: 0,
});

// 定义科室列表
const departmentList = ref([]);

// 定义表单验证规则
const editFormRules = reactive({
  departmentId: [
    {required: true, message: '所属科室不能为空', trigger: 'blur'},
  ],
})

// 定义表单引用
const editFormRef = ref(null);

// 表单重置事件
const handleReset = async (id) => {
  let roomIdList = [];
  for (const room of props.roomList) {
    roomIdList.push(room.id);
  }
  editForm.value = {
    id: roomIdList,
    departmentId: 0,
  };
  console.log('清空表单...');
  // 后台请求科室列表
  let res = await AxiosUtil.get('/api/bsns/departments');
  console.log(res.data);
  if (res.data.code == 20010) {
    let departments = [];
    res.data.departmentList.forEach(item => {
      item.name = "+ " + item.name;
      departments.push(item);
      item.children.forEach(child => {
        child.name = "••• " + child.name;
        departments.push(child);
      })
    });
    // 更新科室列表
    departmentList.value = departments;
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
}

// 定义关闭对话框前的确认操作
const handleClose = (done) => {
  ElMessageBox.confirm('确认在未保存的情况下关闭对话框？')
      .then(() => {
        done()
      })
      .catch(() => {
        // catch error
      })
}

// 定义对话框关闭事件
const handleCancel = () => {
  changeDepartmentDialogVisible.value = false;
}

// 定义确认按钮事件
const handleConfirm = () => {
  // 表单验证
  editFormRef.value.validate(async valid => {
    if (valid) {
      // 表单验证通过
      let url, res;
      console.log('提交修改...');
      url = '/api/bsns/rooms/updateDepartment/' + editForm.value.id.join(',') + '/' + editForm.value.departmentId;
      console.log('url=', url);
      res = await AxiosUtil.put(url, editForm.value);
      console.log("修改诊室科室 res.data= ", res.data);
      if (res.data.code == 20020 || res.data.code == 20030) {
        ElMessage({
          message: res.data.msg,
          type: 'success',
        });
        emit('handleSubmit'); // 触发自定义事件，使父组件刷新数据
        handleCancel(); // 关闭对话框
      } else {
        ElMessage({
          message: res.data.msg,
          type: 'error',
        });
      }
    }
  });
}

// 当对话框重新显示时重置表单
watch(changeDepartmentDialogVisible, (newValue) => {
  if (newValue == true) {
    handleReset();
  }
})

</script>


<script>
export default {
  name: "EditDialog"
}
</script>

<style scoped>

</style>